// XXX: Should we make this a peer dependency?
// eslint-disable-next-line import/no-extraneous-dependencies, import/no-unresolved, import/extensions
import { getStorybook } from '@kadira/storybook';

export const runtime = 'storybook';

export function renderSpec({ runtime: specRuntime, input }) {
  if (specRuntime !== runtime) {
    throw new Error(`Storybook plugin cannot handle ${specRuntime} specs`);
  }

  const { kind, name } = input;

  const story = getStorybook()
    .find(c => c.kind === kind)
    .stories.find(s => s.name === name);

  return story.render({
    kind,
    name,
  });
}

export function specs() {
  return getStorybook()
    .map(({ kind, stories }) =>
      stories.map(({ name }) => ({
        name,
        componentName: kind,
        runtime,
        input: JSON.stringify({
          kind,
          name,
        }),
      }))
    )
    .reduce((a, b) => [...a, ...b], []); // flatten
}

export default {
  runtime,
  renderSpec,
  specs,
};
